"use client";
import React, { useState, useEffect } from 'react';
import { motion } from 'framer-motion';
import { Shield, Zap, RefreshCw, Link2, CheckCircle, AlertCircle, TrendingUp } from 'lucide-react';
import { useMetaStats } from '@/hooks/useMetaStats';

const TradePage = () => {
    const [dxtradeCreds, setDxtradeCreds] = useState({
        username: '',
        password: '',
        server: 'liquid', // Default based on user request
        accountId: 0
    });
    const [isLinking, setIsLinking] = useState(false);
    const [linkStatus, setLinkStatus] = useState<'idle' | 'success' | 'error'>('idle');
    const [copierActive, setCopierActive] = useState(false);
    const [connectionStats, setConnectionStats] = useState<any>(null);

    // Poll for connection status
    useEffect(() => {
        const checkStatus = async () => {
            try {
                const res = await fetch('http://localhost:8000/verify-connection');
                const data = await res.json();
                setConnectionStats(data);
                if (data.dxtrade_copier === 'active') {
                    setCopierActive(true);
                }
            } catch (e) {
                console.error("Failed to fetch connection status", e);
            }
        };

        checkStatus();
        const interval = setInterval(checkStatus, 5000);
        return () => clearInterval(interval);
    }, []);

    const handleLinkAccount = async (e: React.FormEvent) => {
        e.preventDefault();
        setIsLinking(true);
        setLinkStatus('idle');

        try {
            const res = await fetch('http://localhost:8000/dxtrade/login', {
                method: 'POST',
                headers: { 'Content-Type': 'application/json' },
                body: JSON.stringify(dxtradeCreds)
            });

            if (res.ok) {
                setLinkStatus('success');
                setCopierActive(true);
            } else {
                setLinkStatus('error');
            }
        } catch (error) {
            setLinkStatus('error');
        } finally {
            setIsLinking(false);
        }
    };

    return (
        <div className="p-8 max-w-7xl mx-auto space-y-8 text-white">
            <motion.div
                initial={{ opacity: 0, y: -20 }}
                animate={{ opacity: 1, y: 0 }}
                className="flex justify-between items-center"
            >
                <div>
                    <h1 className="text-3xl font-bold bg-clip-text text-transparent bg-gradient-to-r from-white to-gray-400">
                        Verstige OS Copy Trading
                    </h1>
                    <p className="text-gray-400 mt-2">
                        Connect your Liquid Brokers (DxTrade) account to mirror official Verstige strategies.
                    </p>
                </div>
                <div className={`px-4 py-2 rounded-full border ${copierActive ? 'border-green-500/50 bg-green-500/10 text-green-400' : 'border-gray-700 bg-gray-800 text-gray-400'} flex items-center gap-2`}>
                    <div className={`w-2 h-2 rounded-full ${copierActive ? 'bg-green-500 animate-pulse' : 'bg-gray-500'}`} />
                    {copierActive ? 'SYSTEM ACTIVE' : 'SYSTEM IDLE'}
                </div>
            </motion.div>

            <div className="grid grid-cols-1 md:grid-cols-2 gap-8">
                {/* Link Account Card */}
                <motion.div
                    initial={{ opacity: 0, scale: 0.95 }}
                    animate={{ opacity: 1, scale: 1 }}
                    transition={{ delay: 0.1 }}
                    className="p-6 rounded-2xl border border-white/10 bg-white/5 backdrop-blur-md"
                >
                    <div className="flex items-center gap-3 mb-6">
                        <div className="p-3 rounded-lg bg-blue-500/20">
                            <Link2 className="text-blue-400" size={24} />
                        </div>
                        <h2 className="text-xl font-semibold">Connect Account</h2>
                    </div>

                    <form onSubmit={handleLinkAccount} className="space-y-4">
                        <div>
                            <label className="block text-sm text-gray-400 mb-1">DxTrade Username</label>
                            <input
                                type="text"
                                className="w-full bg-black/20 border border-white/10 rounded-lg p-3 focus:outline-none focus:border-blue-500/50 transition"
                                placeholder="julylan.johnson@gmail.com"
                                value={dxtradeCreds.username}
                                onChange={e => setDxtradeCreds({ ...dxtradeCreds, username: e.target.value })}
                            />
                        </div>
                        <div>
                            <label className="block text-sm text-gray-400 mb-1">Password</label>
                            <input
                                type="password"
                                className="w-full bg-black/20 border border-white/10 rounded-lg p-3 focus:outline-none focus:border-blue-500/50 transition"
                                value={dxtradeCreds.password}
                                onChange={e => setDxtradeCreds({ ...dxtradeCreds, password: e.target.value })}
                            />
                        </div>
                        <div>
                            <label className="block text-sm text-gray-400 mb-1">Server / Vendor</label>
                            <input
                                type="text"
                                className="w-full bg-black/20 border border-white/10 rounded-lg p-3 focus:outline-none focus:border-blue-500/50 transition"
                                value={dxtradeCreds.server}
                                onChange={e => setDxtradeCreds({ ...dxtradeCreds, server: e.target.value })}
                            />
                        </div>

                        <button
                            type="submit"
                            disabled={isLinking || copierActive}
                            className={`w-full py-3 rounded-lg font-medium transition flex items-center justify-center gap-2
                                ${copierActive
                                    ? 'bg-green-500/20 text-green-400 cursor-default'
                                    : 'bg-blue-600 hover:bg-blue-500 text-white'}`}
                        >
                            {isLinking ? (
                                <RefreshCw className="animate-spin" size={18} />
                            ) : copierActive ? (
                                <>
                                    <CheckCircle size={18} />
                                    Account Linked
                                </>
                            ) : (
                                "Link Account"
                            )}
                        </button>

                        {linkStatus === 'error' && (
                            <div className="p-3 rounded-lg bg-red-500/10 border border-red-500/20 text-red-400 text-sm flex items-center gap-2">
                                <AlertCircle size={16} />
                                Connection failed. Verify credentials.
                            </div>
                        )}
                        {linkStatus === 'success' && (
                            <div className="p-3 rounded-lg bg-green-500/10 border border-green-500/20 text-green-400 text-sm flex items-center gap-2">
                                <CheckCircle size={16} />
                                Connection established successfully.
                            </div>
                        )}
                    </form>
                </motion.div>

                {/* Status / Master Stats Card */}
                <motion.div
                    initial={{ opacity: 0, scale: 0.95 }}
                    animate={{ opacity: 1, scale: 1 }}
                    transition={{ delay: 0.2 }}
                    className="space-y-6"
                >
                    <div className="p-6 rounded-2xl border border-white/10 bg-white/5 backdrop-blur-md">
                        <div className="flex items-center gap-3 mb-6">
                            <div className="p-3 rounded-lg bg-purple-500/20">
                                <Shield className="text-purple-400" size={24} />
                            </div>
                            <div>
                                <h2 className="text-xl font-semibold">Master Strategy</h2>
                                <p className="text-xs text-gray-400">Verstige OS Mainline</p>
                            </div>
                        </div>

                        <div className="grid grid-cols-2 gap-4">
                            <div className="p-4 rounded-lg bg-white/5 border border-white/5">
                                <div className="text-gray-400 text-sm mb-1">Instrument</div>
                                <div className="text-2xl font-mono font-bold text-white">US30</div>
                            </div>
                            <div className="p-4 rounded-lg bg-white/5 border border-white/5">
                                <div className="text-gray-400 text-sm mb-1">Win Rate</div>
                                <div className="text-2xl font-mono font-bold text-green-400">92%</div>
                            </div>
                            <div className="p-4 rounded-lg bg-white/5 border border-white/5 col-span-2">
                                <div className="text-gray-400 text-sm mb-1">Live Status</div>
                                <div className="flex items-center justify-between">
                                    <span className="text-white">Waiting for setup...</span>
                                    <div className="flex gap-1">
                                        <div className="w-1.5 h-6 bg-green-500/40 rounded-sm"></div>
                                        <div className="w-1.5 h-6 bg-green-500/60 rounded-sm"></div>
                                        <div className="w-1.5 h-6 bg-green-500 rounded-sm"></div>
                                        <div className="w-1.5 h-6 bg-green-500/60 rounded-sm"></div>
                                        <div className="w-1.5 h-6 bg-green-500/40 rounded-sm"></div>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>

                    <div className="p-6 rounded-2xl border border-white/10 bg-gradient-to-br from-blue-900/20 to-purple-900/20 backdrop-blur-md">
                        <div className="flex items-start gap-4">
                            <Zap className="text-yellow-400 shrink-0" size={24} />
                            <div>
                                <h3 className="font-semibold text-lg text-white">Fast Execution Mode</h3>
                                <p className="text-sm text-gray-400 mt-1">
                                    Trades are copied instantaneously via WebSocket locally. Ensure this dashboard remains open for optimal performance.
                                </p>
                            </div>
                        </div>
                    </div>
                </motion.div>
            </div>
        </div>
    );
};

export default TradePage;
